html {
    background-color: transparent;
}

.card-container {
    border: 1px solid #FFFFFF;

    transition: 0.5s;
    animation: ease-in-out;
    align-content: center;

    height: 100%;
}

.card-content {
    padding: 20px;
    font-weight: 200;
    
}

.card-content h3 {
    font-size: 32px;
}

.card-content p {
    /* font-family: "Inter", "sans-serif"; */
    font-size: 22px;
    margin-top: 16px;
}

.card-content h3,
p {
    color: #FFFFFF;
    margin: 0;
    padding: 0;
    /* letter-spacing: 2px; */

    text-align: left;

    transition: 0.5s;
    height: 100%;
}

.card-container:hover {
    transform: scale(1.05);
}

.card-1 {
    grid-area: 1 / 1 / 2 / 3;
}

.card-2 {
    grid-area: 1 / 3 / 2 / 5;
}

.card-3 {
    grid-area: 1 / 5 / 2 / 7;
}

.card-4 {
    grid-area: 2 / 1 / 3 / 4;
}

.card-5 {
    grid-area: 2 / 4 / 3 / 7;
}

.card-bot {
    grid-area: "bot";
}

.grid-logic {
    width: 80vw;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: auto auto;
    row-gap: 1.5rem;
    column-gap: 1.5rem;
    grid-auto-flow: dense;
    max-width: 1200px;
}

@media screen and (max-width: 1024px) {

    .card-container:hover {
        transform: none;
    }

    .card-container {
        display: flex;
        margin: 0 2rem;
        /* text-align: center; */
    }

    /* .card-container h3, p{
        text-align: center;
    } */
}